<template>
<v-app>
  <AppNavigationDrawer />

  <AppToolbar v-if='!isHome' />

  <VuePerfectScrollbar class='scroll-area' :settings='settings'>    
    <v-content>
      <v-layout justify-center class='white'>
        <v-flex xs12 :lg11='!isHome && !isSankey' :xl10='!isHome && !isSankey'>
          <v-slide-y-reverse-transition mode='out-in'>
            <router-view />
          </v-slide-y-reverse-transition>
        </v-flex>
      </v-layout>
    </v-content>

    <AppFooter />
  </VuePerfectScrollbar>
</v-app>
</template>


<script>
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import AppNavigationDrawer from '@/components/shared/NavigationDrawer'
import AppToolbar          from '@/components/shared/Toolbar'
import AppFooter           from '@/components/shared/Footer'

export default {
  data: () => ({
    settings: {
      minScrollbarLength: 300,
      maxScrollbarLength: 500,
      suppressScrollX   : true,
    },
    offsetTop: 0,
  }),
  created() {
    this.$vuetify.theme.primary   = '#C7A754'
    this.$vuetify.theme.techblue  = '#004D8C'
    this.$vuetify.theme.techgold  = '#DDB95C'
    this.$vuetify.theme.buzzgold  = '#EAAA00'
  },
  computed: {
    isHome() {
      return this.$route.path === '/'
    },
    isSankey() {
      return this.$route.path === '/water-energy'
    }
  },
  methods: {
  },
  watch: {
    '$vuetify.breakpoint'(to) {
      if(to.mdAndDown) _.debounce(() => this.drawer = false, 100)()
    }
  },
  mounted() {
    window.addEventListener('scroll', e => {
      this.offsetTop = window.scrollY
    })
  },
  components: {
    AppToolbar,
    VuePerfectScrollbar,
    AppNavigationDrawer,
    AppFooter,
  }
}
</script>

<style lang='stylus'>
@import url('https://fonts.googleapis.com/css?family=Poppins')

::-webkit-scrollbar
  width  : 0 !important
  display: none !important
  
.bold
  font-weight: bold !important
</style>


<style lang='stylus' scoped>
.scroll-area
  position: relative
  width   : 100%
  height  : 100%

</style>
