import Vue    from 'vue'
import Router from 'vue-router'
import Main   from './components/Main.vue'

Vue.use(Router)

export default new Router({
  base: process.env.BASE_URL,
  routes: [{
    path: '/',
    name: 'main',
    component: Main
  }, {
    path: '/water-energy',
    name: 'water-energy',
    component: () => import('@/components/WaterEnergy.vue')
  }, {
    path: '/dashboard',
    name: 'dashboard',
    component: () => import('@/components/Dashboard2.vue')
  }, {
    path: '/calendar',
    name: 'calendar',
    component: () => import('@/components/CalendarMain.vue')
  }]
})
